import React from 'react'
import './index.scss'
import Esfera from './esfera'
const Habilities = () => {
    return(
        <>
        <div className="habilitiesContainer">
            <div className="lefthContainer">
                <div className="headerContainer">
                    <h3>Conocimientos Tecnicos</h3>
                    <p>A continuación se muestran algunas de las habilidades que he ido adquiriendo durante mi carrera profesional.</p>
                </div>
                <div className="frontContainer">
                    <p>Desarrollo FrontEnd</p>
                    <div>
                        <ul>
                            <li>HTML5</li>
                            <li>CSS3, Flexbox, CSS Grid</li>
                            <li>JavaScript</li>
                            <li>Responsive Web Design</li>
                            <li>UX/UI</li>
                            <li>Accesibilidad, Usabilidad</li>
                        </ul>
                        <ul>
                            <li>SublimeText</li>
                            <li>Bootstrap</li>
                            <li>Git / GitHub</li>                    
                        </ul>
                    </div>
                </div>
                <div className="endContainer">
                    <p>Desarrollo BackEnd</p>
                    <div>
                        <ul>
                            <li>JavaScript</li>
                            <li>PHP</li>                   
                        </ul>
                        <ul>
                            <li>Node Js</li>
                            <li>Apache</li>
                            <li>MySQL</li>                    
                            <li>Xampp / Lampp</li>
                        </ul>
                    </div>
                </div>
                <div className="otherContainer">
                    <p>Otros</p>
                    <div>                    
                        <ul>
                            <li>Python</li>                    
                        </ul>
                        <ul>
                            <li>Keras</li>
                            <li>TensorFlow</li>
                            <li>OpenpyXL</li>                    
                        </ul>
                    </div>                                
                </div>
            </div>
            
            <div className="rightContainer">
                <Esfera></Esfera>
            </div>
        </div>
        </>
    )
}
export default Habilities;