import React from 'react'
import './index.scss'
import Facebook from '../../../img/contact/facebook.svg'
import Git from '../../../img/contact/github.svg'
import Insta from '../../../img/contact/instagram.svg'
import Linkedin from '../../../img/contact/linkedin.svg'

const SocialMedia = () => {
    <script src="https://kit.fontawesome.com/f6816dd194.js" crossorigin="anonymous"></script>
    return(
        <>
            <div className="main center">
                <div className="box center">
                   
                    <a href="https://www.facebook.com/twist.campos/" target="_blank">
                        <img src={Facebook} alt="IconFacebook"></img>
                        <div>
                            <label>Facebook</label>
                        </div>                        
                    </a>
                    <a href="https://github.com/VladiOrt" target="_blank">
                        <img src={Git} alt="IconGit"></img>
                        <div>
                            <label>Git Hub</label>
                        </div>    
                    </a>
                    <a href="https://www.instagram.com/vladiiortega/?hl=es-la" target="_blank">
                        <img src={Insta} alt="IconInsta"></img>
                        <div>
                            <label>Instagram</label>
                        </div>    
                    </a>
                    <a href="https://www.linkedin.com/in/vladimir-ortega-campos-690519113/" target="_blank">
                        <img src={Linkedin} alt="IconLinkedin"></img>
                        <div>
                            <label>Linkedin</label>
                        </div>    
                    </a>                    
                </div>
            </div>
        </>
    )
}
export default SocialMedia;