import React from 'react'
import './index.scss'
import php from '../../../img/php.png'
import nodejs from '../../../img/nodejs.png'
import react from  '../../../img/react.png'
import bootstrap from '../../../img/bootstrap.png'
import css from '../../../img/css3.png'
import python from '../../../img/python.png'
import js from '../../../img/js.png'
import html from '../../../img/html.png'
import git from '../../../img/git.png'

import apache from '../../../img/apache.png'
import phpmyadmin from '../../../img/phpmyadmin.png'
import js2 from '../../../img/js2.png'
import tensorflow from '../../../img/tensorflow.png'
import vs from '../../../img/vs.png'
import mysql from '../../../img/mysql.png'
import xampp from '../../../img/xampp.png'


const Esfera = () => {
    return(
        <>
     <div className="world">
        <div className="cube">
            <div className="circunferenciaCero">
                <img src={php} alt="IconoPhp"></img>                
                <img src={html} alt="IconoHtml"></img>
                <img src={mysql} alt="Iconmysql"></img>
            </div>
            <div className="circunferenciaUno">
                <img src={nodejs} alt="IconoNode"></img>
                <img src={git} alt="IconoGit"></img>
                <img src={xampp} alt="Iconxampp"></img>
            </div>
            <div className="circunferenciaDos">
                <img src={react} alt="IconoReact"></img>
                <img src={apache} alt="IconoApache"></img>
            </div>
            <div className="circunferenciaTres">
                <img src={bootstrap} alt="IconoBootstrap"></img>
                <img src={phpmyadmin} alt="IconoMyAdmin"></img>
            </div>                
            <div className="circunferenciaCuatro">
                <img src={css} alt="IconoCss"></img>
                <img src={js2} alt="Iconojs2"></img>
            </div>
            <div className="circunferenciaCinco">
                <img src={python} alt="IconoPython"></img>
                <img src={tensorflow} alt="Iconotensorflow"></img>
            </div>
            <div className="circunferenciaSeis">
                <img src={js} alt="IconoJs"></img>
                <img src={vs} alt="Iconovs"></img>
            </div>
        </div>
    </div>
        </>
    )
}
export default Esfera;