import React from 'react'
import './index.scss'
import ImgMe from '../../img/about/me.jpeg'


const AboutMe = () => {
    return(
        <>
          <div className="aboutmeContainer">    
            <div className="fondo1">
            </div>
            <div className="fondo2">
            </div>
            <div className="containerContent">
                <div className="ImgAboutMe">
                    <img src={ImgMe} alt="Image about me"></img>
                </div>
                <div className="textContainerAboutMe">
                    <p>
                     En la actualidad me encuentro creando, actualizando y manteniendo desarrollos web. Tanto del lado del cliente como del servidor.
                        Estoy en constante aprendizaje y desarrollo profesional, para ofrecer las mejores soluciones de acuerdo a las necesidades particulares de cada proyecto.
                    </p>
                </div>
            </div>
        </div>
        </>
    )
}
export default AboutMe;