import React,{useState} from 'react'
import './index.scss'
import ImgHome from '../../../img/menu/home.png'
import ImgAbout from '../../../img/menu/about.png'
import ImgHabilities from '../../../img/menu/habilities.png'
import ImgWork from '../../../img/menu/work.png'
import ImgContact from '../../../img/menu/contact.png'






const Buttons = () => {

    function RedireccionarAdelante(valor){
        var x =valor * window.innerWidth
        window.scroll({
            top: 0,
            left: x,
            behavior: 'smooth'
        })
       setCount(count + 1)        
    }

    function RedireccionarAtras(valor){
        var x =valor * window.innerWidth
        window.scroll({
            top: 0,
            left: x,
            behavior: 'smooth'
        })        
        setCount(count - 1)         
    }
    
    function Redireccionar(valor){
        var x =valor * window.innerWidth
        window.scroll({
            top: 0,
            left: x,
            behavior: 'smooth'
        })
       setCount(valor)        
    }

    const [count, setCount] = useState(0);
    let Menor = "<"
    let Mayor = ">"
  
    return(
        <>  
        <div className="containerHeadersButtons"> 
            <div className="homeButtonContainer" onClick={() => Redireccionar(0)}>
                HOME
                {/* 
                <img src={ImgHome}></img> 
                <div className="homeTextContainer">
                    <div>H</div>
                    <div>O</div>
                    <div>M</div>
                    <div>E</div>
                </div>
                */}
            </div>
            
            <div className="aboutButtonContainer" onClick={() => Redireccionar(1)}>
                
                ABOUT ME
                {/* 
                <img src={ImgAbout} alt="AboutIcon"></img> 
                <div className="aboutTextContainer">
                    <div>A</div>
                    <div>B</div>
                    <div>O</div>
                    <div>U</div>
                    <div>T</div>              
                </div>
                */}
            </div>
            
            <div className="habilitiesButtonContainer" onClick={() => Redireccionar(2)}>                             
                SKILLS  
                {/* 
                <img src={ImgHabilities} alt="HabilitiesIcon"></img> 
                <div className="habilitiesTextContainer">
                    <div>S</div>
                    <div>K</div>
                    <div>I</div>
                    <div>L</div>
                    <div>L</div>
                    <div>S</div>
                </div>
                */} 
            </div>
           
            <div className="workButtonContainer" onClick={() => Redireccionar(3)}>
               
                WORK
                {/* 
                <img src={ImgWork} alt="WorkIcon"></img> 
                <div className="workTextContainer">
                    <div>W</div>
                    <div>O</div>
                    <div>R</div>
                    <div>K</div>                    
                </div>
                */}
            </div>
            
            <div className="contactButtonContainer" onClick={() => Redireccionar(4)}>                
                CONTACT
                {/* 
                <img src={ImgContact} alt="ContactIcon"></img> 
                <div className="contactTextContainer">
                    <div>S</div>
                    <div>O</div>
                    <div>C</div>
                    <div>I</div>
                    <div>A</div>
                    <div>L</div>
                </div>
                */}
            </div>
            
        </div>
       
        {
            (count < 4) ?
                <button onClick={() => RedireccionarAdelante(count+1)}>
                    <div className="containerButtonNext">
                        <div className="containerTextNext">                                           
                           {Mayor}                                           
                        </div>     
                    </div>
                </button>     
            :
            <button>
                <div className="containerButtonNext">                 
                </div>
            </button>     
        }
        
        {   
            (count > 0) ?
                <button onClick={() => RedireccionarAtras(count-1)}>
                    <div className="containerButtonBack">                        
                        <div className="containerTextBack">                
                            {Menor}                             
                        </div>
                    </div>
                </button>   
            :
                <button onClick={() => RedireccionarAtras(count)}>
                    <div className="containerButtonBack">            
                    </div>
                </button>   
            }                        
        </>
    )
}

export default Buttons;